import { Component, OnInit } from '@angular/core';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
})
export class HomeComponent implements OnInit {

  constructor() { }
  ngOnInit() {
  }
  loginFlag = true;
  toggleForms(){
    if (this.loginFlag) {
      this.loginFlag = false;
    }
    else{
      this.loginFlag = true;
    }
  }

}



